<template>
  <v-container>
    <v-layout
            wrap
            justify-center
    >
      <v-flex
              xs10 sm10 md10 lg10
      >
        <v-breadcrumbs :items="items">
          <template v-slot:divider>
            <v-icon>mdi-chevron-right</v-icon>
          </template>
        </v-breadcrumbs>
      <app-carousel
              :user="user"
              :portfolio="portfolio"
      ></app-carousel>
      </v-flex>
      <v-flex
              xs10 sm10 md10 lg10
      >
        <app-content
                :portfolio="portfolio"
                :user="user"
                :auth="auth"
                :filterCategory="filterCate"
        ></app-content>
      </v-flex>
      <v-flex
            xs10 sm10 dm10 lg10
      >
        <app-also-like
        :categoryId="categoryId"
        ></app-also-like>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import appCarousel from '../../components/detail/Carousel'
  import appContent from '../../components/detail/Content'
  import appAlsoLike from '../../components/detail/AlsoLike'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    components: {
      appCarousel,
      appContent,
      appAlsoLike
    },
    data: () => ({
      categoryId: '',
      portfolio: {},
      user: {},
      items: [
        {
          text: 'Home',
          to: { name: 'Home' },
          exact: true
        },
        {
          text: 'Detail',
          to: { name: 'Detail' },
        }
      ]
    }),
    computed: {
      ...mapGetters([
        'auth',
      ])
    },
    methods: {
      ...mapActions([
         'filterCategory'
      ]),
      async fetchPortfolio () {
        const results = await this.$axios.get('home/detail', { params: { _id:  this.$route.params.id } })
        const portfolio = results.data.portfolio
        this.portfolio = portfolio
        this.user = portfolio.author
        this.categoryId = portfolio.categoryId._id
      },
      async filterCate (value) {
          this.$router.push({ name: 'Home', query: { by_category: value } })
      }
    },
    mounted () {
      this.$io.on('acceptWork', (id) => {
        if(this.user._id === id) {
          this.fetchPortfolio()
        }
      })
      this.$io.on('updatePortfolio', (id) => {
        if(this.$route.params.id === id) {
          this.fetchPortfolio()
        }
      })
      this.$io.on('deletePortfolio', (id) => {
        if(this.$route.params.id === id) {
          this.$router.push({ name: 'Home' })
        }
      })
    },
    created () {
      this.fetchPortfolio()
    }
  }
</script>
